<template>
  <div class="abody">
    <div class="waicengdiv">
      <div class="neicengdiv"></div>
      <span></span>
    </div>
  </div>
</template>

<script>
export default {
  name: "Langjiazai"
}
</script>

<style scoped>
/*0*/
.abody{
  padding: 0;
  margin: 0;
}
.waicengdiv{
  /**/
  position: absolute;
  /*div*/
  width: 100vw;
  /*div*/
  height: 100vh;
  /**/
  display: flex;
  /**/
  justify-content: center;
  /*flexflex*/
  align-items: center;
  /**/
  background-color: #000;
}
.neicengdiv{
  /**/
  position: absolute;
  /*150px*/
  width: 150px;
  /*150px*/
  height: 150px;
  /*50%*/
  border-radius: 50%;
  /**/
  display: flex;
  /**/
  justify-content: center;
  /*flexflex*/
  align-items: center;
  /**/
  background: linear-gradient(0deg,
  #4B0082,
  #3A91CF 20%,
  #01FFFF 40%,
  #ffff00 60%,
  #FFA500 80%,
  #FF0000 100%);
  animation: donghua 1s linear infinite;
}
/*before:*/
.neicengdiv:before{
  /**/
  content: "";
  /**/
  position: absolute;
  /*150px*/
  width: 100px;
  /*150px*/
  height: 100px;
  /*50%*/
  border-radius: 50%;
  /**/
  background: linear-gradient(0deg,
  #4B0082,
  #3A91CF 20%,
  #01FFFF 40%,
  #ffff00 60%,
  #FFA500 80%,
  #FF0000 100%);
  /*filter(): blur();*/
  filter: blur(35px);
  /**/
  display: flex;
  /**/
  justify-content: center;
  /*flexflex*/
  align-items: center;
}
/*after:*/
.neicengdiv:after{
  /**/
  content: "";
  /**/
  width: 120px;
  /**/
  height: 120px;
  /**/
  background: #000;
  /**/
  position: absolute;
  /**/
  border-radius: 50%;
  /**/
  display: flex;
  /**/
  justify-content: center;
  /*flexflex*/
  align-items: center;
}
span{
  color: #e8e5e5;
  position: absolute;
  font-size: 12px;
}
@keyframes donghua {
  0%{
    transform: rotate(0deg);
  }
  100%{
    transform: rotate(360deg);
  }
}
</style>



